body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}


.container {
    margin-bottom: 100px;
}

.card-deck {
    margin-bottom: 20px;
}

.revealed-card:hover {
    cursor: pointer;
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
}

.revealed-card {
    z-index: 1;
}
.container {
    margin: 0 auto;
    text-align: center;
}

#empty-text {
    margin-top: 140px;
}

.game-text {
    margin-bottom: 40px;
}

#heroImg{
    display: block;
    width: 50%;
    margin: 50px auto 10px auto;
}

.jumbotron {
    border-bottom: 5px solid #ffffff;
}

#reset-game {
    width: 100%;
    margin-top: 20px;
    margin-bottom: 20px;
}


